import React,{useState} from "react";
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import './style.css'
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Grid from '@mui/material/Grid';
// import 'math'


const AddExpenseBar = ({confirmedItems})=>{
    let [item, setItem] = useState('')
    let [amount, setAmount] = useState('')
    let [category, setcategory] = React.useState('');

    const enteredValues=(event)=>{
        let value = event.target.value
        if (event.target.type === 'number'){
            setAmount(value)
        }else if(event.target.type === 'text'){
            setItem(value)
        }
    }

    const notifyError = (text) => toast.error(text);
    // const notifySucess = (text)=> toast.success(text);
    

    const addItem = ()=>{
            if(item && amount && category){
                let createdTime2 = new Date()
                let newObj2 = {}
                newObj2['name'] = item 
                newObj2['amount'] = (parseInt(amount))
                newObj2["category"] = category
                newObj2["createdTime2"] = createdTime2
                confirmedItems({...newObj2})
                setItem('')
                setAmount('')
                setcategory('')
            }else{
                notifyError('Enter Item, Amount and Category')
            }
    }


    const handleChangeCategory = (event)=>{
        setcategory(event.target.value)
    }

    return (
        <div className='exBarContainer'>
            {/* <div className='ExpenseBar'> */}
            <Grid container spacing={1}>
                <Grid item xl={3} md={3} xs={6}>
                <TextField size='small' id="outlined-basic" label="Enter Item" variant="outlined" onChange={enteredValues} value={item}/>
                </Grid>
                <Grid item xl={3} md={3} xs={6}>
                <TextField size='small' id="outlined-basic" label="Amount" variant="outlined" type='number' InputProps={{ inputProps: { min: 1 } }} onChange={enteredValues}value={amount}/>
                </Grid>
                <Grid item xl={3} md={3} xs={6}>
                <FormControl size='small' fullWidth>
                    <InputLabel id="demo-simple-select-label">Category</InputLabel>
                    <Select
                        labelId="demo-simple-select-label"
                        id="demo-simple-select"
                        value={category}
                        label="category"
                        onChange={handleChangeCategory}
                    >
                        <MenuItem value={"Food"}>Food</MenuItem>
                        <MenuItem value={"Transport"}>Transport</MenuItem>
                        <MenuItem value={"Entertainment"}>Entertainment</MenuItem>
                        <MenuItem value={"Utility"}>Utility</MenuItem>
                    </Select>
                </FormControl>
                </Grid>
                <Grid item xl={2} md={3} xs={6}>
                <Button onClick={addItem}>Add</Button>
                </Grid>
            </Grid>
            <ToastContainer
            position="bottom-center"
            autoClose={3000}
            hideProgressBar={false}
            newestOnTop={false}
            closeOnClick
            rtl={false}
            pauseOnFocusLoss
            draggable
            pauseOnHover
             />
        </div>
    )
}

export default AddExpenseBar;