import React from "react";
import './style.css'
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Grid from '@mui/material/Grid';
import DatePicker from 'react-date-picker';

const TodayExpense = ({selectedDateExpense})=>{
    const [value, onChange] = React.useState(new Date());
    const total = selectedDateExpense.map(item=>item.amount)

    return (
        <Grid container direction="column">
            <Grid item>
            {`Add to date: `}
            <DatePicker
                    onChange={onChange}
                    value={value}
                    maxDate = {new Date()}
                    clearIcon = {null}
                    format = 'dd-MM-y'
                />
            </Grid>
            <div className = 'box1'>
            <Grid container alignItems="center" >
                <Grid item xl ={6} md={6} xs={12} className='padding0'>
                    <Grid className = 'marginB5'><span className = 'expenseDate'>{ value && (value.toDateString())} Expense</span></Grid>
                </Grid>
                <Grid item xl ={4} md={4} xs={8}>
                <FormControl className = 'marginB5' fullWidth size="small">
                        <InputLabel id="demo-simple-select-label">Category</InputLabel>
                        <Select
                            labelId="demo-simple-select-label"
                            id="demo-simple-select"
                            value={''}
                            label="category"
                            onChange={()=>{}}
                        >
                            <MenuItem value={"Food"}>Food</MenuItem>
                            <MenuItem value={"Transport"}>Transport</MenuItem>
                            <MenuItem value={"Entertainment"}>Entertainment</MenuItem>
                            <MenuItem value={"Utility"}>Utility</MenuItem>
                        </Select>
                    </FormControl>
                </Grid>
                <Grid item xl ={4} md={4} xs={4}>
                </Grid>
            </Grid>
            <Grid container>
                <Grid container direction = 'column' spacing = {2} wrap = 'wrap' sx = {{minHeight:'25vh', maxHeight:'35vh', overflowX:'auto', width:'100%'}}>
                    {
                        selectedDateExpense && selectedDateExpense.length>0 && 
                        selectedDateExpense.map(item=>{
                        return <Grid item >{`${item["name"]} ${item["category"]} ${item["amount"]}₹`}<button>Delete</button> </Grid>
                    })
                    }
                </Grid>
            </Grid>
            <Grid item sx={{textAlign:'right', marginRight:'10px'}}>
                {
                     selectedDateExpense && selectedDateExpense.length>0 && (`Total Cost ${total.reduce((i, j )=>i+j)}`)
                }
            </Grid>
            </div>
        </Grid>
    )
}


export default TodayExpense;